angular.module('freshAir', ['ngRoute']);

angular.module('freshAir')
  .config(['$routeProvider', '$locationProvider', function($routeProvider, $locationProvider){
    
    $routeProvider
      .when('/', {
        templateUrl : '/views/main.html',
        controller : 'mainCtrl'
      });
      
    $locationProvider.html5Mode(true);
      
}]);



